"use client"

import { useEffect, useRef, useState } from "react"
import { gsap } from "gsap"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Sparkles } from "lucide-react"
import MathAnimationBox from "../public/animations/lottie/math-operations.json"
import ModuleIcon from "../components/module-icon"
import { initGSAP } from "@/lib/gsap-config"

export default function HomePage() {
  const [email, setEmail] = useState("")
  const logoRef = useRef<HTMLDivElement>(null)
  const titleRef = useRef<HTMLHeadingElement>(null)
  const descriptionRef = useRef<HTMLParagraphElement>(null)
  const registerButtonRef = useRef<HTMLDivElement>(null)
  const modulesRef = useRef<HTMLDivElement>(null)
  const emailSectionRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    initGSAP()

    // Animaci√≥n de entrada secuencial
    const tl = gsap.timeline()

    tl.from(logoRef.current, {
      x: -100,
      opacity: 0,
      duration: 1,
      ease: "back.out(1.7)",
    })
      .from(
        ".login-btn",
        {
          x: 100,
          opacity: 0,
          duration: 1,
          ease: "back.out(1.7)",
        },
        "<0.2",
      )
      .from(
        titleRef.current,
        {
          y: 50,
          opacity: 0,
          duration: 1.2,
          ease: "power3.out",
        },
        "-=0.5",
      )
      .from(
        descriptionRef.current,
        {
          y: 30,
          opacity: 0,
          duration: 1,
          ease: "power2.out",
        },
        "-=0.8",
      )
      .from(
        ".animation-box",
        {
          scale: 0.8,
          opacity: 0,
          duration: 1,
          stagger: 0.2,
          ease: "elastic.out(1, 0.5)",
        },
        "-=0.6",
      )
      .from(
        registerButtonRef.current,
        {
          scale: 0,
          rotation: 180,
          duration: 1.2,
          ease: "elastic.out(1, 0.3)",
        },
        "-=0.4",
      )
      .from(
        ".module-item",
        {
          y: 40,
          opacity: 0,
          stagger: 0.1,
          duration: 0.8,
          ease: "power2.out",
        },
        "-=0.6",
      )
      .from(
        emailSectionRef.current,
        {
          y: 30,
          opacity: 0,
          duration: 1,
          ease: "power2.out",
        },
        "-=0.4",
      )

    // Animaciones continuas
    gsap.to(registerButtonRef.current, {
      y: -8,
      duration: 2,
      repeat: -1,
      yoyo: true,
      ease: "sine.inOut",
    })

    // Animaci√≥n sutil del logo
    gsap.to(logoRef.current?.querySelector(".brain-icon"), {
      rotation: 10,
      duration: 3,
      repeat: -1,
      yoyo: true,
      ease: "sine.inOut",
    })

    // Part√≠culas flotantes de estrellas
    createFloatingStars()
  }, [])

  const createFloatingStars = () => {
    for (let i = 0; i < 8; i++) {
      const star = document.createElement("div")
      star.innerHTML = "‚≠ê"
      star.className = "fixed pointer-events-none text-yellow-400 opacity-20 text-sm"
      star.style.left = Math.random() * 100 + "%"
      star.style.top = Math.random() * 100 + "%"
      document.body.appendChild(star)

      gsap.to(star, {
        y: -20,
        rotation: 360,
        duration: 4 + Math.random() * 2,
        repeat: -1,
        yoyo: true,
        ease: "sine.inOut",
        delay: Math.random() * 2,
      })

      gsap.to(star, {
        opacity: 0.4,
        duration: 2,
        repeat: -1,
        yoyo: true,
        ease: "sine.inOut",
        delay: Math.random() * 1,
      })
    }
  }

  const modules = [
    {
      name: "CONJUNTOS",
      component: <ModuleIcon.Conjuntos />,
      description: "Agrupa elementos",
    },
    {
      name: "NUMERACI√ìN",
      component: <ModuleIcon.Numeracion />,
      description: "Aprende n√∫meros",
    },
    {
      name: "SUMA",
      component: <ModuleIcon.Suma />,
      description: "Suma f√°cil",
    },
    {
      name: "RESTA",
      component: <ModuleIcon.Resta />,
      description: "Resta simple",
    },
    {
      name: "MULTIPLICACI√ìN",
      component: <ModuleIcon.Multiplicacion />,
      description: "Multiplica",
    },
    {
      name: "DIVISI√ìN",
      component: <ModuleIcon.Division />,
      description: "Divide y reparte",
    },
    {
      name: "GEOMETR√çA",
      component: <ModuleIcon.Geometria />,
      description: "Formas divertidas",
    },
  ]

  return (
    <div className="min-h-screen" style={{ backgroundColor: "#F5F0E1" }}>
      {/* Header */}
      <header className="flex justify-between items-center p-6">
        <div ref={logoRef} className="flex items-center gap-3">
          <div className="brain-icon relative">
            <div className="w-16 h-16 relative">
              {/* Cerebro bicolor */}
              <div className="absolute inset-0 rounded-full overflow-hidden">
                <div className="w-1/2 h-full bg-blue-500 absolute left-0 rounded-l-full"></div>
                <div className="w-1/2 h-full bg-orange-500 absolute right-0 rounded-r-full"></div>
              </div>
              {/* Detalles del cerebro */}
              <div className="absolute inset-2 border-2 border-white rounded-full opacity-30"></div>
              <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-1 h-8 bg-white opacity-50"></div>
            </div>
          </div>
          <h1 className="text-2xl font-bold" style={{ color: "#2C3E50" }}>
            MentaM√°tica
          </h1>
        </div>

        <Button
          className="login-btn font-bold px-6 py-2 rounded-full shadow-lg hover:shadow-xl transition-all duration-300"
          style={{
            backgroundColor: "#F7DC6F",
            color: "#2C3E50",
            border: "2px solid #F4D03F",
          }}
        >
          INICIAR SESI√ìN
        </Button>
      </header>

      {/* Main Content */}
      <main className="container mx-auto px-6 py-8">
        {/* Hero Section */}
        <div className="text-center mb-12">
          <h2 ref={titleRef} className="text-5xl font-bold mb-6" style={{ color: "#2C3E50" }}>
            Piensa, juega y resuelve
          </h2>
          <p ref={descriptionRef} className="text-lg text-gray-700 max-w-2xl mx-auto mb-12">
            MentaM√°tica es una aplicaci√≥n donde encontrar√°s una forma inteligente y divertida de aprender matem√°ticas.
          </p>
        </div>

        {/* Interactive Section with Animations */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 mb-16">
          {/* Left Animation */}
          <div className="flex justify-center">
            <MathAnimationBox type="counting" />
          </div>

          {/* Center Registration */}
          <div className="flex flex-col items-center justify-center">
            <div ref={registerButtonRef}>
              <Button
                size="lg"
                className="font-bold px-12 py-6 rounded-full text-xl shadow-2xl hover:shadow-3xl transition-all duration-300 transform hover:scale-105"
                style={{
                  backgroundColor: "#D2B48C",
                  color: "white",
                  border: "3px solid #C19A6B",
                }}
              >
                <Sparkles className="w-6 h-6 mr-2" />
                REGISTRARSE
              </Button>
            </div>
          </div>

          {/* Right Animation */}
          <div className="flex justify-center">
            <MathAnimationBox type="operations" />
          </div>
        </div>

        {/* Learning Modules */}
        <div ref={modulesRef} className="mb-16">
          <h3 className="text-2xl font-bold text-center mb-8" style={{ color: "#2C3E50" }}>
            Que aprender√°s aqu√≠:
          </h3>

          <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-7 gap-6">
            {modules.map((module, index) => (
              <div
                key={module.name}
                className="module-item text-center cursor-pointer group"
                onMouseEnter={(e) => {
                  gsap.to(e.currentTarget, {
                    y: -10,
                    scale: 1.05,
                    duration: 0.3,
                    ease: "power2.out",
                  })
                }}
                onMouseLeave={(e) => {
                  gsap.to(e.currentTarget, {
                    y: 0,
                    scale: 1,
                    duration: 0.3,
                    ease: "power2.out",
                  })
                }}
              >
                <div className="mb-3 flex justify-center transform group-hover:scale-110 transition-transform duration-300">
                  {module.component}
                </div>
                <h4 className="font-bold text-sm mb-1" style={{ color: "#2C3E50" }}>
                  {module.name}
                </h4>
                <p className="text-xs text-gray-600">{module.description}</p>
              </div>
            ))}
          </div>
        </div>

        {/* Email Subscription */}
        <div ref={emailSectionRef} className="max-w-md mx-auto text-center">
          <p className="text-gray-700 mb-4">Si deseas m√°s informaci√≥n escribe tu correo electr√≥nico</p>
          <div className="relative">
            <Input
              type="email"
              placeholder="example@hotmail.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full px-4 py-3 rounded-full border-2 text-center"
              style={{
                backgroundColor: "#D2B48C",
                borderColor: "#C19A6B",
                color: "white",
              }}
            />
          </div>
        </div>
      </main>
    </div>
  )
}